.calendar {
  display: inline-block;
  position: relative;
  padding: 16px 32px;
  margin: 10px;
  border-radius: 10px;
  min-width: 340px;
  box-sizing: border-box;
  font-family: almoni-dl;
  font-weight: 700;
  box-shadow: 0 0 12px 0 #e8e8e8;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .calendar .icon-close {
    position: absolute;
    top: 14px;
    left: 16px;
    width: 14px;
    height: 14px;
    fill: #b7b9b7;
    cursor: pointer; }
    .calendar .icon-close:hover {
      fill: #636365; }
  .calendar .title {
    font-size: 19px;
    margin: 25px 0 16px 0; }
  .calendar .days-of-week {
    margin: 20px 0 14px 0; }
    .calendar .days-of-week .day {
      display: inline-block;
      width: 33px;
      height: 33px;
      margin: 0 3px;
      direction: rtl;
      color: #999b99;
      font-size: 14px; }
  .calendar .month .week {
    margin: 4px 0; }
    .calendar .month .week .day {
      display: inline-block;
      width: 33px;
      height: 33px;
      margin: 0 3px;
      border-radius: 50%;
      color: #b7b9b7;
      text-align: center;
      line-height: 33px;
      font-size: 12px;
      font-family: 'Open Sans', sans-serif;
      vertical-align: bottom; }
      .calendar .month .week .day.selectable {
        color: #51847d;
        background-color: #dde9ea;
        cursor: pointer;
        -webkit-transition: all 0.15s ease;
        transition: all 0.15s ease; }
        .calendar .month .week .day.selectable.selected {
          color: white !important;
          background-color: #51847d !important;
          cursor: default; }
        .calendar .month .week .day.selectable:hover {
          color: white;
          background-color: #b6d2ce; }
  .calendar .legend ul {
    list-style-type: none; }
    .calendar .legend ul li {
      display: flex;
      flex-direction: row-reverse;
      align-items: center; }
      .calendar .legend ul li .circle {
        display: inline-block;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        margin-left: 12px; }
      .calendar .legend ul li .pink {
        background: #f9eaf1; }
      .calendar .legend ul li .blue {
        background: #dde9ea; }
  .calendar .month-picker {
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .calendar .month-picker .icon-container {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 31px;
      height: 31px;
      border: 1px solid #999b99;
      border-radius: 3px;
      cursor: pointer; }
      .calendar .month-picker .icon-container.disable {
        opacity: 0.5;
        cursor: auto;
        cursor: initial; }
      .calendar .month-picker .icon-container .icon {
        width: 14px;
        height: 14px;
        fill: #636365; }
    .calendar .month-picker .months {
      position: relative;
      text-align: right;
      width: 176px;
      cursor: pointer;
      border-radius: 5px 5px 0 0; }
      .calendar .month-picker .months::before {
        content: "";
        position: absolute;
        top: calc(50% - 2.5px);
        left: 10px;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 5px 5px 0 5px;
        border-color: #636365 transparent transparent transparent; }
      .calendar .month-picker .months.opened::before {
        border-width: 0 5px 5px 5px;
        border-color: transparent transparent #636365 transparent; }
      .calendar .month-picker .months .selected {
        padding: 7px 8px;
        border-bottom: 1px solid #b7b9b7;
        -webkit-transition: inherit;
        transition: inherit; }
        .calendar .month-picker .months .selected:hover {
          border-bottom: 1px solid #636365; }
      .calendar .month-picker .months ul {
        padding: 0;
        direction: rtl;
        display: none;
        position: absolute;
        top: 33px;
        right: 0;
        margin: 0;
        width: 100%;
        height: 300px;
        box-sizing: border-box;
        overflow-y: auto;
        list-style-type: none;
        border-radius: 0 0 5px 5px; }
        .calendar .month-picker .months ul li {
          padding: 5px 8px; }
          .calendar .month-picker .months ul li.hovered {
            background-color: #ecf2f2;
            color: #51847d; }
      .calendar .month-picker .months.opened {
        background-color: #fafafa;
        box-shadow: 0 0 12px 0 #e8e8e8; }
        .calendar .month-picker .months.opened ul {
          display: block;
          background-color: #fafafa;
          box-shadow: 0px 12px 16px 0px #d9d9d9; }
          .calendar .month-picker .months.opened ul::-webkit-scrollbar {
            width: 15px;
            padding: 0 2px; }
          .calendar .month-picker .months.opened ul::-webkit-scrollbar-thumb {
            background: #b7b9b7;
            border: 4px solid rgba(0, 0, 0, 0);
            background-clip: padding-box;
            border-radius: 9px; }

